.header {
	padding-top: 45px;
	@include media("<tablet") {
		padding-top: 25px;
	}
	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include media("<tablet") {
			flex-direction: column;
		}
	}
	&__logo {
		font-weight: 600;
		font-size: 25px;
		line-height: 34px;
		@include media("<tablet") {
			margin-bottom: 20px;
		}
		&::first-letter {
			color: #2fc67d;
		}
	}
	&__nav {
		display: flex;
		align-items: center;
	}
	&__nav-link {
		margin-right: 25px;
		a {
			font-family: "Inter", sans-serif;
			font-weight: 500;
			font-size: 18px;
			line-height: 25px;
			color: #a3a3a8;
		}
	}
}

.info-block-list {
	.home-block {
		&:not(:first-child) {
			padding-top: 0;
		}
	}
}
